.App {
  text-align: center;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.from{
  text-align: center;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  border-radius: 20px;
  border-style:dashed;
  border-color: black;
  background-color: rgb(255, 154, 59);
}

.shop{
  border-radius: 20px;
  border-style: inset;
  border-color: black;
  background-color: aqua;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  color: deeppink;
}

.nimi{
  background-color: rgb(211, 162, 204);
  font-family: "Comic Sans MS", "Comic Sans", cursive;
}

p{
  color: rgb(236, 2, 225);
  font-family: "Comic Sans MS", "Comic Sans", cursive;
}

.rocks{
  background-color: rgb(185, 185, 185);
  border-style:inset;
  border-color: rgb(150, 204, 255);
  border-width: 20px;
  flex-direction: row;
  color: fuchsia;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
}

h3{
  color: rgb(255, 0, 234);
  font-family: "Comic Sans MS", "Comic Sans", cursive;
}

.App-header {
  background-color: #ffd000;
  min-height: 25vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
